const datas = [
  { id: 0, category: "blockchain", icon: nodejs, title: "Node JS" },
  { id: 1, category: "blockchain", icon: laravel, title: "Laravel" },
  { id: 2, category: "blockchain", icon: python, title: "Python" },
  { id: 3, category: "blockchain", icon: golang, title: "Golang" },
  { id: 4, category: "blockchain", icon: djangoicon, title: "Django" },
  { id: 5, category: "mobileapp", icon: apple, title: "IOS" },
  { id: 6, category: "mobileapp", icon: formkit_android, title: "Android" },
  { id: 7, category: "mobileapp", icon: react, title: "React" },
  { id: 8, category: "mobileapp", icon: devicon_flutter, title: "Flutter" },
  {
    id: 9,
    category: "mobileapp",
    icon: devicon_androidstudio,
    title: "Android Studio",
  },
  { id: 10, category: "backend", icon: nodejs, title: "Node JS" },
  { id: 11, category: "backend", icon: laravel, title: "Laravel" },
  { id: 12, category: "backend", icon: python, title: "Python" },
  { id: 13, category: "backend", icon: golang, title: "Golang" },
  { id: 14, category: "backend", icon: djangoicon, title: "Django" },
  { id: 15, category: "frontend", icon: react, title: "React" },
  { id: 16, category: "frontend", icon: nuxtjs, title: "Angular" },
  { id: 17, category: "devops", icon: azure, title: "Azure" },
  { id: 18, category: "devops", icon: aws_dark, title: "AWS" },
  { id: 19, category: "devops", icon: tensorflow, title: "TensorFlow" },
];

const PrivateBlockchainTechnologies = () => {
  const [activeCategory, setActiveCategory] = useState("blockchain");

  const categories = [...new Set(datas.map((item) => item.category))].map(
    (category) => ({
      key: category,
      name: category.charAt(0).toUpperCase() + category.slice(1),
    })
  );

  const filteredData = datas.filter((data) => data.category === activeCategory);

  return (
    <section className="private-blockchain-hero-wrapper item-center gap-md">
      <h3 className="about-h3 c-text-center">Our Development Technologies</h3>
      <div className="flex-center gap-md category-btns">
        {categories.map((cat) => (
          <button
            key={cat.key}
            className={`category-btn about-p-two ${
              activeCategory === cat.key ? "category-btn-active" : ""
            }`}
            onClick={() => setActiveCategory(cat.key)}
          >
            {cat.name}
          </button>
        ))}
      </div>
      <div className="gap-md flex-center private-blockchain-enterprise-cards">
        {filteredData.map((data) => (
          <div
            key={data.id}
            className="flex-center gap-sm private-blockchain-enterprise-card"
          >
            <img src={data.icon} alt={data.title} />
            <p className="about-p-two fw-600">{data.title}</p>
          </div>
        ))}
      </div>
    </section>
  );
};

export default PrivateBlockchainTechnologies;
